.history{
    box-sizing: border-box;
    >ul{
        display: flex;
        flex-direction: column;
        list-style:decimal;
        row-gap: 12px;
        li{
            cursor: pointer;
            border: 1px solid #888;
            border-radius: 7px;
            box-shadow: 3px 3px #ccc;
            padding: 3px 12px;
            
        }
        li:active{
            border: 3px solid #888;
            padding: 1px 9px;
            box-shadow: none;
        }
    }
}